import React, { Component } from "react";
import ApplicationPositions from "./ApplicationPositions/ApplicationPositions";
import { ApplicationStepsTypes } from "./ApplicationSteps/ApplicationSteps";
import PositionRequirement from "./PositionRequirement/PositionRequirement";
import ExamCenter from "./ExamCenter/ExamCenter";
import * as Interfaces from "../../interfaces/index";
import { connect } from "react-redux";
import {
  Auth,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  positionsStoreInterface,
  FC_Access,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
} from "../../actions";
import { StoreState } from "../../reducers";
import {
  applicationStoreInterface,
  initialApplicationInterface,
} from "../../actions/application";
import {
  ApplicationInterface,
  ApplicationPeriodGet,
  SESSION_PLAN_KEYS,
} from "../../interfaces/application";
import { PositionAPI } from "../../interfaces/index";
import { Redirect } from "react-router";
import { UserAccessList } from "../../utils/auth_access";
import Loading from "../../components/Loading/Loading";
import { DATE } from "../../utils/functions";
import {
  FindActiveInPlans,
  FindPlan,
} from "../../components/ApplicationPeriods/ApplicationPeriods";
import { IoMdInformationCircleOutline } from "react-icons/io";
import { Link } from "react-router-dom";

interface ApplicationState {
  currentStep: ApplicationStepsTypes;
  currentPosition: Interfaces.PositionAPI | null;
  redirectState: boolean;
  loading: boolean;
}

interface ApplicationProps {
  auth: Auth;
  application: applicationStoreInterface;
  initiateApplication: (initial: initialApplicationInterface) => void;
  createApplication: (
    user_id: string,
    application: ApplicationInterface
  ) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  selectPosition: (position: PositionAPI | null) => void;
  FC_Access: Function;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
}

export class _Application extends Component<
  ApplicationProps,
  ApplicationState
> {
  constructor(props: ApplicationProps) {
    super(props);

    this.state = {
      currentStep: ApplicationStepsTypes.Step1,
      currentPosition: null,
      redirectState: false,
      loading: false,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  componentDidMount = () => {
    if (
      this.props.periods.selected_period === null ||
      this.props.periods.selected_period.is_active === false
    ) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
    if (this.props.periods.applicationPeriods !== null) {
      let myFind = this.props.periods.applicationPeriods.find(
        (itm) => itm.is_active === true
      );
      if (myFind !== undefined) {
        this.props.setSelectedPeriod(myFind);
      }
    }
    // this.props.initiateApplication()
    if (this.props.application.apply === undefined) {
      this.props.initiateApplication({
        user_id: this.props.auth.user.user_id
          ? this.props.auth.user.user_id
          : "waiting",
        selectedPosition: null,
        currentStep: ApplicationStepsTypes.Step1,
        positions: null,
        doTestStatus: {
          status: false,
        },
        selectedLocation: [],
      });
      if (
        this.props.application.applications.length > 0 &&
        this.props.application.apply === undefined
      ) {
        this.setState({ redirectState: true });
      } else {
        this.setState({ redirectState: false });
      }
    }
    // console.log("Application: ", this.props.application);
  };
  render() {
    // if (this.props.application.applications.length > 0 && this.props.application.apply === undefined) {
    //   return <Redirect to="/applications" />
    // }
    if (this.props.auth.user.user_id) {
      if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).create) {
        return <Redirect to="/apps-page" />;
      }
    }
    return (
      <div>
        {this.props.periods.selected_period !== null &&
        FindActiveInPlans(this.props.periods.selected_period.plans).find(
          (itm) => itm.type_key === SESSION_PLAN_KEYS.APPLICATION
        ) === undefined ? (
          <div className="bg-white rounded-md px-6 py-6 my-6 text-center">
            <div className="flex flex-col items-center">
              <IoMdInformationCircleOutline className="text-5xl md:text-8xl text-yellow-700" />
            </div>
            <span className="text-yellow-700 font-extrabold text-4xl">
              Application is closed
            </span>
            {this.props.periods.selected_period.plans.find(
              (itm) => itm.type_key === SESSION_PLAN_KEYS.APPLICATION
            ) !== undefined ? (
              <div>
                <span className="text-gray-500">
                  Make sure you access the system between the following dates
                </span>
                <div className="flex flex-row items-center justify-center gap-4 mt-4">
                  <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                    Start date:{" "}
                    {DATE(
                      this.props.periods.selected_period.plans.find(
                        (itm) => itm.type_key === SESSION_PLAN_KEYS.APPLICATION
                      )!.start_date,
                      "DD/MM/YYYY"
                    )}
                  </span>
                  <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                    Ending date:{" "}
                    {DATE(
                      this.props.periods.selected_period.plans.find(
                        (itm) => itm.type_key === SESSION_PLAN_KEYS.APPLICATION
                      )!.end_date,
                      "DD/MM/YYYY"
                    )}
                  </span>
                </div>
              </div>
            ) : (
              <div className="text-sm text-gray-500">
                For this session, no application phase has been activated yet,
                <br />
                please keep in touch with the system to know the time for it or
                <br />
                contact the support center near your location
              </div>
            )}
            <div className="mt-6">
              <Link
                className="text-blue-700 font-bold underline mt-6"
                to="/applications"
              >
                Go back to my applications
              </Link>
            </div>
          </div>
        ) : (
          <div
            className={`px-2 ${
              this.props.auth.user.user_id
                ? " -ml-4 lg:-ml-4"
                : "max-w-7xl mx-auto px-2 md:px-12 lg:px-24"
            } `}
          >
            {/* Step 2 */}
            {this.props.application.apply !== undefined &&
            this.props.application.apply.currentStep ===
              ApplicationStepsTypes.Step2 ? (
              // <DoTest
              //   currentPosition={this.props.application.apply.selectedPosition}
              //   setCurrentPosition={this.props.selectPosition}
              //   setApplicationStep={this.props.setApplicationStep}
              // />
              <div>Do psychometric tex</div>
            ) : (
              this.props.application.apply !== undefined && (
                <div className="">
                  {this.props.application.apply.selectedPosition !== null &&
                  this.props.application.apply.currentStep ===
                    ApplicationStepsTypes.Step1
                    ? ""
                    : this.props.periods.applicationPeriods !== null &&
                      this.props.periods.applicationPeriods.find(
                        (test) => test.is_active === true
                      ) !== undefined && (
                        <div className="mt-2 pt-2 pb-1 border-b-2">
                          <div className="grid md:grid-cols-2 gap-2">
                            <div className="col-span-1 flex flex-col">
                              <span className="text-blue-600 text-3xl lg:text-3xl font-extrabold mb-1">
                                {this.props.application.apply.currentStep ===
                                ApplicationStepsTypes.Step1
                                  ? "Published positions"
                                  : this.props.application.apply.currentStep ===
                                    ApplicationStepsTypes.Step3
                                  ? "Examination Center"
                                  : "Candidate application"}
                              </span>
                              {this.props.application.apply.selectedPosition !==
                              null ? (
                                <div className="flex flex-row items-center gap-3 text-base text-gray-900 font-bold">
                                  <div className="text-blue-200 font-bold bg-blue-700 rounded px-2 border border-blue-300">
                                    {this.props.application.apply
                                      .currentStep ===
                                    ApplicationStepsTypes.Step1
                                      ? "Step 1 of 2:"
                                      : "Step 2 of 2"}
                                  </div>{" "}
                                  Choose examination center for each exam
                                </div>
                              ) : (
                                <span className="text-gray-900 text-xs lg:text-sm">
                                  List of positions ready for the application,
                                  choose a position
                                </span>
                              )}
                            </div>
                            {this.state.loading === true ? (
                              <div className="bg-white rounded px-3 pt-3">
                                <Loading />
                              </div>
                            ) : (
                              <div className="col-span-1 flex flex-col md:items-end -mb-2 md:-mt-2 bg-white p-2 md:p-0 md:bg-transparent rounded md:rounded-none">
                                <div className="flex flex-row gap-2 md:text-right">
                                  <span className="text-gray-800">
                                    Session:{" "}
                                  </span>
                                  <span className="font-extrabold text-blue-600">
                                    {
                                      this.props.periods.selected_period
                                        ?.session_plan_name
                                    }
                                  </span>
                                </div>
                                {this.props.periods.selected_period &&
                                FindPlan(
                                  this.props.periods.selected_period.plans,
                                  SESSION_PLAN_KEYS.APPLICATION
                                ) !== null ? (
                                  <>
                                    <div className="flex flex-row gap-2 text-sm text-right">
                                      <span className="text-gray-800">
                                        Start date:
                                      </span>
                                      <span className="font-extrabold">
                                        {this.props.periods.selected_period &&
                                          DATE(
                                            FindPlan(
                                              this.props.periods.selected_period
                                                .plans,
                                              SESSION_PLAN_KEYS.APPLICATION
                                            )!.start_date,
                                            "DD/MM/YYYY"
                                          )}
                                      </span>
                                    </div>
                                    <div className="flex flex-row gap-2 text-sm text-right items-center">
                                      <span className="text-gray-800">
                                        Ending date:
                                      </span>
                                      <span className="font-extrabold text-yellow-900 bg-yellow-100 border-2 border-white rounded px-2 py-1">
                                        {this.props.periods.selected_period &&
                                          DATE(
                                            FindPlan(
                                              this.props.periods.selected_period
                                                .plans,
                                              SESSION_PLAN_KEYS.APPLICATION
                                            )!.end_date,
                                            "DD/MM/YYYY"
                                          )}
                                      </span>
                                    </div>
                                  </>
                                ) : (
                                  <div>Application is not open</div>
                                )}
                              </div>
                            )}
                          </div>
                        </div>
                      )}

                  <div className="my-2">
                    <div className="w-full">
                      {/* Step one */}
                      {this.props.application.apply.currentStep ===
                        ApplicationStepsTypes.Step1 &&
                        (this.props.application.apply.selectedPosition ===
                        null ? (
                          <ApplicationPositions
                            currentPosition={
                              this.props.application.apply.selectedPosition
                            }
                            setCurrentPosition={this.props.selectPosition}
                          />
                        ) : (
                          <PositionRequirement
                            currentPosition={
                              this.props.application.apply.selectedPosition
                            }
                            setCurrentPosition={this.props.selectPosition}
                            setApplicationStep={this.props.setApplicationStep}
                          />
                        ))}
                      {/* Step 3 */}
                      {this.props.application.apply.currentStep ===
                        ApplicationStepsTypes.Step3 && (
                        <ExamCenter
                          currentPosition={
                            this.props.application.apply.selectedPosition
                          }
                          setCurrentPosition={this.props.selectPosition}
                          setApplicationStep={this.props.setApplicationStep}
                        />
                      )}
                    </div>
                  </div>
                </div>
              )
            )}
          </div>
        )}

        <div className="bg-gray-800 mt-60">
          <div className=" text-center flex flex-col py-16 text-white gap-6 px-2">
            <span>
              TRS sponsored by the Mastercard Foundation, Developed by the{" "}
              <a
                className="underline font-extrabold text-blue-300 hover:text-blue-600"
                href="https://binaryhub.rw/"
                rel="noreferrer"
                target="_blank"
                title="UR Holdings Ltd"
              >
                University of Rwanda Holdings Group Ltd
              </a>
            </span>
            <span className="font-bold text-xl">
              Copyright © 2021-{new Date().getFullYear()} MINEDUC
            </span>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  application,
  positions,
  periods,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    application: application,
    positions,
    periods: periods,
  };
};

export const Application = connect(mapStateToProps, {
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  FC_Access,
  loadApplicationPeriods,
  setSelectedPeriod,
})(_Application);
